@import 'https://fonts.googleapis.com/css?family=Baloo+Paaji';

.contenedor {
    width: 95%;
    padding: 15px 1px 15px 5%;
    background: black;
    animation:infinite resplandorAnimation 3s;
}

@keyframes resplandorAnimation {
    0%,100%{
      box-shadow: 1px 1px 20px 1px rgb(255, 255, 255);
      background-color: rgb(18, 19, 19);
    }
    
    50%{
    box-shadow: 0px 0px 0px rgb(227, 215, 215);
    background-color: rgb(0, 0, 0);
    }
  
}

.medidas {
    font-size: 1.1rem;
    font-family: 'Baloo Paaji', cursive;
    color: azure;
    display: flex;
    align-items: center;
    animation-duration: 3.5s;
    animation-name: slidein;
}

.colores {
    font-size: 1.1rem;
    font-family: 'Baloo Paaji', cursive;
    color: azure;
    display: flex;
    align-items: center;
    animation-duration: 3.5s;
    animation-name: slidein;
}

.precio {
    font-size: 1.1rem;
    font-family: 'Courier New', Courier, monospace;
    color: azure;
    display: flex;
    align-items: center;

    animation-duration: 3.5s;
    animation-name: slidein;
    
}

.icono {
    font-size: 3.5rem;
}

@keyframes slidein {
    from {
      margin-left: 100%;
    }
  
    to {
      margin-left: 0%;
    }
  }


